import React, { useState } from "react";
import { checkData, doSearch, updatePoints } from "../ts/diploma";
import { ids, subjects } from "../ts/constants";

let test = { dis: true };

//test = { LN: "Ибраев", FN: "Дмитрий", MN: "Александрович", BD: "2002-08-30", dis: false };

function DefaultInput({ id, label, def, ...inputProps }) {
    const [val, setVal] = useState(def || "");
    const validate = e => {
        e.preventDefault();

        const target = e.target;
        let newVal = target.value;

        if (target.type === "number") {
            newVal = updatePoints(Number(newVal), target.id) || "";
        }

        setVal(newVal);
    };

    return (
        <p>
            <label htmlFor={id}>{label}</label>
            <input id={id} {...inputProps} value={val} onChange={validate} />
        </p>
    );
}

const EgeForm = React.memo(function EgeForm() {
    console.log("render egeform");
    const getSubj = id => {
        const subj = subjects[id];
        return subj[0].toUpperCase() + subj.slice(1);
    };
    const Forma = arr => <form>
        {arr.map(id => <DefaultInput
            key={id}
            id={id}
            label={getSubj(id)}
            type="number"
            min="0" max="100"
            placeholder="60"
        />)}
    </form>;

    return (
        <div className="ege" onChange={() => checkData(false)} >
            {Forma(ids.slice(0, 4))}
            {Forma(ids.slice(4))}
        </div>
    );
});

function FioForm({ checkFio }) {
    console.log("render fioform");
    const fio = [
        {
            id: "LN", type: "text",
            label: "Фамилия",
            maxLength: 20,
            placeholder: "Иванов",
            def: test.LN
        },
        {
            id: "FN", type: "text",
            label: "Имя",
            maxLength: 20,
            placeholder: "Иван",
            def: test.FN
        },
        {
            id: "MN", type: "text",
            label: "Отчество",
            maxLength: 20,
            placeholder: "Иванович",
            def: test.MN
        },
        {
            id: "BD", type: "date",
            label: "Дата рождения",
            max: "2005-01-01",
            min: "1996-01-01",
            def: test.BD
        }
    ];

    return (
        <div className="fio" onChange={() => checkData(true)} >
            <form id="fio_form" autoComplete="on" onChange={e => checkFio(e.target.form)}>
                {fio.map(obj => <DefaultInput key={obj.id} {...obj} />)}
            </form>
        </div>
    );
};

function SearchBtn({ isDisabled, setDisable }) {
    const [btnName, setBtnName] = useState("Проверить");
    return (
        <button disabled={isDisabled} onClick={() => doSearch(setBtnName, setDisable)}>{btnName}</button>
    );
}

function SearchForm() {
    const [isDisabled, setDisable] = useState(test.dis);
    const props = { isDisabled, setDisable };
    const checkFio = form => setDisable(!Boolean(form[0].value && form[1].value));

    return (
        <div>
            <div className="search">
                <FioForm checkFio={checkFio} />
                <EgeForm />
            </div>
            <SearchBtn {...props} />
        </div>
    );
}

export default SearchForm;